.haru-ripple {
  position: relative;
  overflow: hidden;
}
.haru-ripple .wave {
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.haru-ripple .animating.spread-enter-active {
  transition: transform 0.8s cubic-bezier(0.157, 0.72, 0.386, 0.987), opacity 0.6s linear;
}
.haru-ripple .vanish.spread-leave-active {
  transition: opacity 0.4s linear;
}



