














































































































































































































.haru-ripple
  position: relative
  overflow: hidden

  // wave default style
  .wave
    pointer-events: none
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    border-radius: 50%
  
  // cubic-bezier(0, 0, 0.2, 1)
  .animating.spread-enter-active
    transition: transform .8s cubic-bezier(.157, .72, .386, .987), opacity .6s linear

  .vanish.spread-leave-active
    transition: opacity 0.4s linear



